import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import BugDetails from './views/BugDetails.vue'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  // base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/bugs/:id',
      name: 'details',
      props: true,
      component: BugDetails
    },
    {
      path: '*',
      redirect: Home
    }

  ]
})
